.icon {
  display: block;
  height: $size-icon;
  width: $size-icon;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;

  &--menu {
    background-image: url(../images/menu-button.svg);
  }

  &--cross {
    background-image: url(../images/closing-button.svg);
  }
}
